:root {
    --violeta: #b82df9;
    --lila: #b470f0;
    --rosa: #c50598;
}

.contenedor {
    max-width: 700px;
    width: 100%;
    height: 100%;
    max-height: 90vh;
    overflow: auto;
    box-shadow: 0 2px 10px rgba(139,139,139,.25);
    border-radius: 3px;
    display: grid;
    grid-template-rows: auto auto 1fr;
}

/*--- --- --- HEADER --- --- --- */

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    padding: 0 20px;
    background: var(--lila);
    color: #fff;
    border-radius: 3px 3px 0px 0px;
}

.header__titulo {
    font-weight: 400;
    font-size: 24px;
}

.header__boton {
    background: none;
    font-size: 16px;
    color: #fff;
    border: none;
    cursor: pointer;
    background: var(--rosa); 
    padding: 5px 10px;
    border-radius: 5px;
    display: flex;
    align-items: center;
}

.header__icono-boton {
    font-size: 14px;
    margin-left: 10px;
}

/*--- --- --- FORMULARIO AGREGAR --- --- --- */

.formulario-tareas {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: 20px;
    box-shadow: 0 2px 10px rgba(139,139,139,.25);
}

.formulario-tareas__input {
    border: none;
    padding: 15px;
    width: 100%;
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
    border-bottom: 2px solid #b8b8b8;
}

.formulario-tareas__input:focus {
    outline: none;
    border-bottom: 2px solid var(--lila);
}

.formulario-tareas__btn {
    position: absolute;
    bottom: 40px;
    right: 20px;
    border: none;
    background: none;
    cursor: pointer;
}

.formulario-tareas__icono-btn {
    font-size: 22px;
    color: var(--violeta);
}

/*--- --- --- LISTA TAREAS --- --- --- */

.lista-tareas {
    list-style: none;
    height: 100%;
    overflow: auto;
}

.lista-tareas__tarea {
    height: 60px;
    padding: 0 20px;
    align-items: center;
    color: #4b4b4b;
    font-size: 18px;
    display: grid;
    grid-template-columns: auto 1fr auto;
}

.lista-tareas__texto {
    text-align: left;
    width: 100%;
}

.lista-tareas__tarea:hover .lista-tareas__icono-accion {
    opacity: 0.5;
}

.lista-tareas__tarea:nth-child(even) {
    background: #f3f3f3;
}

.lista-tareas__contenedor-botones {
    display: flex;
}

.lista-tareas__icono {
    cursor: pointer;
    font-size: 16px;
}

.lista-tareas__icono-check {
    margin-right: 20px;
    font-size: 26px;
}

.lista-tareas__icono-accion {
    margin-left: 20px;
    opacity: 0;
    transition: .3s ease all;
}

.lista-tareas__mensaje {
    text-align: center;
    color: #b8b8b8;
    font-size: 20px;
    padding: 40px 20px;
}

/*--- --- --- FORMULARIO EDITAR TAREAS --- --- --- */

.formulario-editar-tarea {
    display: flex;
}

.formulario-editar-tarea__input {
    width: 100%;
    border: none;
    border-bottom: 2px solid #b8b8b8;
    background: none;
    font-size: 19px;
    font-family: 'Roboto', sans-serif;
}

.formulario-editar-tarea__input:focus {
    outline: none;
    border-bottom: 2px solid var(--lila);
}

.formulario-editar-tarea__btn {
    border: none;
    border-radius: 3px;
    cursor: pointer;
    font-size: 14px;
    margin-left: 20px;
    background: var(--violeta);
    color:#f3f3f3;
    padding: 5px 10px;    
}